<template>

  <div class="flash-main" @touchmove.prevent>
    <div class="flash">
      <div class="flash-top">
        <h1>专业的电子化采购软件开发商</h1>
      </div>
      <!-- <div class="flash-bottom"></div> -->
    </div>
    <div class="flash-loding" @click="handleSubmit">跳过<span plain round>{{ computedTime }}s</span></div>
  </div>

</template>

<script>
import { mapMutations } from 'vuex'
// import * as dd from 'dingtalk-jsapi'
import * as dd from 'dingtalk-jsapi'
import { ddMixin } from '../../../common/js/mixin'
var baseURL = ''

export default {
  baseURL: '',
  name: 'Flash',
  mixins: [ddMixin],
  data() {
    return {
      computedTime: 3 // 倒数记时
      // imgUrl: require('../../../assets/flash/flash.png')
    }
  },
  created() {
    this.timer = setInterval(() => {
      this.computedTime--
      if (this.computedTime === 0) {
        clearInterval(this.timer)
        this.handleSubmit()
      }
    }, 1000)

    this.getLXR()
  },

  methods: {
    ...mapMutations([
      'SET_DDTYPE'
    ]),
    getLXR() {
      dd.ready(function() {
        dd.runtime.permission.requestAuthCode({
        // corpId: '',

          corpId: 'ding88fc6e2f569030b335c2f4657eb6378f',
          onSuccess: function(result) {
            baseURL = result

            // this.getLogin(result)
          },
          // eslint-disable-next-line handle-callback-err
          onFail: function() {
          }
        })
      })
    },
    handleSubmit() {
      if (baseURL !== '' && baseURL !== null && baseURL !==
      undefined) {
        this.SET_DDTYPE(true)
        this.SET_SQM(baseURL.code)

        this.$router.push({
          name: 'jbrhome',
          params: { sqm: baseURL
          }})
      } else {
        this.$router.push({
          name: 'jbrlogin'
        })
      }

      this.SET_DDTYPE(true)
      this.SET_SQM(baseURL.code)

      // this.$router.push('/pdf')

      this.$router.push({
        name: 'jbrhome',
        params: { sqm: baseURL
        }})
    }
  }
}
</script>

